<template>
	<div>
		<div style="padding: 0 0.75rem;">
         <div class="login_box">
         	<div class="login">
         		<div class="icon">Login</div>
         		<p>京隆公众号</p>
         	</div>
         
         </div>  
           </div>
           	<p style="font-size: 0.32rem;color:#090909 ;padding: 0 0.75rem;margin-top: 0.3rem;">登陆后该应用将获得以下权限</p>
         	<div style="font-size: 0.3rem;color: #999999;padding: 0 0.75rem;margin-top: 0.1rem;">
         	<div class="circle"></div>	
         		获得你的公开信息 (昵称、头像等)  </div>
		<div class="button" @click="login">确认登录</div>
	
	</div>
</template>

<script>

	export default{
		
		
		methods:{
			login(){
				
				 this.$router.push({name: 'Binding'})
			}
		}
		
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	.login_box{
        width: 100%;
        height:3.8rem ;
        border-bottom:0.02rem solid #cccccc ;
        position: relative;
        .login{
            width:2rem;
            height:2.2rem;
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            margin:auto;
           
            
            .icon{
                width: 1.6rem;
                height: 1.6rem;
                background: #fa8a12;
                font-size: 0.45rem;
                text-align: center;
                line-height: 1.6rem;
                  color: #FFFFFF;
                  margin-left: 0.2rem;
            }
            p{
                margin-top: 0.1rem;
                font-size: 0.4rem;
                color: #333333;
            }
            
            
        }
        
    }
    
.circle{
    width: 0.12rem;
    height: 0.12rem;
    border-radius:50% ;
    background: #999999;
    float: left;
    position: relative;
    top: 0.15rem;
    margin-right: 0.2rem;
    
    
}
.button{
    width: 6.9rem;
    height: 1rem;
    background: #1aad19;
    font-size: 0.34rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 1rem;
    border-radius:0.08rem ;
    margin:1.4rem auto;
}
	
		
		
		
		
		
		
		
</style>


